$(function () {
  select2Init();

  datepickerInit();

  datetimePickerInit();

  ckEditorInit();

  openMenu();

  copyMlInfo();

  responseMessage();

  minimizeMenu();
});
window.showSuccessMessage = function (message) {
  $.toast({
    text: message,
    hideAfter: 2000,
    position: 'top-right',
    stack: false,
    loader: false,
    icon: 'success',
    allowToastClose: false,
  });
};

window.showErrorMessage = function (message) {
  $.toast({
    heading: 'Error',
    hideAfter: 2000,
    text: message,
    position: 'top-right',
    stack: false,
    loader: false,
    icon: 'error',
    allowToastClose: false,
  });
};
function select2Init(div = undefined, className = 'select2') {
  let select2 = $(`select.${className}`);
  if (typeof div !== 'undefined') {
    select2 = div.find(`select.${className}`);

  }
  $.each(select2, function () {
    $(this).select2({
      placeholder: $trans('__dashboard.select.option.default'),
      minimumResultsForSearch: 10,
      tags: $(this).data('allow-tags') || false,
      allowClear: $(this).data('allow-clear') || false,
    });
  });

}

function select2Reset(select) {
  if (select.length) {
    select.select2('val', '');
    select.find('option').not(':first-child').remove();
  }

}

function select2SetValues(select, data) {
  $.each(data, (key, value) => {
    select.append(new Option(value, key, false, false)).trigger('change.select2');
  });

}

function disableField(field, disable) {
  if (typeof disable === 'undefined') {
    disable = true;

  }
  field.prop('disabled', disable);

}

function loadContent(content, removeLoad) {
  if (typeof removeLoad === 'undefined') {
    content.addClass('loading-content position-relative');
  } else {
    content.removeClass('loading-content position-relative');
  }

}

function minimizeMenu() {
  $('.brand-toggle').click(() => {
    $('.page').toggleClass('minimaize-menu');
  });
  $('.open-menu').click(() => {
    $('.page').toggleClass('left-menu-opened');
    $('.open-menu').toggleClass('open-menu-opened');
  });
}


function ckEditorInit(selector = '.ckeditor5') {
  const ckeditorEls = document.querySelectorAll(selector);

  window.CKEditors = window.CKEditors || {};

  ckeditorEls.forEach((item) => {
    const id = item.getAttribute('id');
    if (!id) return;

    ClassicEditor.create(item, {
      cloudServices: {
        tokenUrl: 'https://33333.cke-cs.com/token/dev/ijrDsqFix838Gh3wGO3F77FSW94BwcLXprJ4APSp3XQ26xsUHTi0jcb1hoBt',
        uploadUrl: 'https://33333.cke-cs.com/easyimage/upload/',
      },
    })
      .then((editor) => {
        if (item.hasAttribute('disabled')) {
          editor.isReadOnly = true;
        }

        editor.model.document.on('change:data', () => {
          editor.updateSourceElement();
        });

        window.CKEditors[id] = editor;
        console.log('CKEditor initialized for id:', id);  // Лог для подтверждения успешной инициализации

      })
      .catch((error) => {
        console.error(error);
      });
  });
}


function datepickerInit() {
  const datepickerInputs = $('.datepicker');

  if (datepickerInputs.length) {
    $.each(datepickerInputs, function () {
      const self = $(this);

      self.datetimepicker({
        timepicker: false,
        format: 'd.m.Y',
        scrollMonth: false,
        scrollInput: false,
        onChangeDateTime(dp, $input) {
          let backendVal = '';
          if ($input.val()) {
            backendVal = moment($input.val(), 'DD.MM.YYYY').format($dashboardDates.js.date_format);
          }
          self.siblings('.backend-date-value').val(backendVal);
        },
      });
    });
  }
}

function datetimePickerInit() {
  const datetimePickerInputs = $('.datetimepicker');

  if (datetimePickerInputs.length) {
    $.each(datetimePickerInputs, function () {
      const self = $(this);

      self.datetimepicker({
        format: 'd.m.Y H:i',
        onChangeDateTime(dp, $input) {
          let backendVal = '';
          if ($input.val()) {
            backendVal = moment($input.val(), 'DD.MM.YYYY HH:mm').format($dashboardDates.js.date_time_format);
          }
          self.siblings('.backend-date-value').val(backendVal);
        },
      });
    });
  }
}

function openMenu() {
  $('.open-menu').click(() => {
    $('.page').toggleClass('left-menu-opened');
    $('.open-menu').toggleClass('open-menu-opened');
  });
}

function copyMlInfo() {
  const copyMlButton = $('.copy-ml-info-btn');

  if (copyMlButton.length) {
    copyMlButton.click(function () {
      const self = $(this);
      const supportedLocales = self.data('supported-locales');

      const currentTab = $('.tab-pane.active');
      const currentTabInputs = currentTab.find(':input');
      const currentLangCode = currentTab.attr('id').split('__').pop();

      supportedLocales.forEach(function (localeCode) {
        if (localeCode !== currentLangCode) {
          const toTab = $(`#__mls__tab__${localeCode}`);
          $.each(currentTabInputs, function () {
            const inputName = $(this).attr('name');
            if (inputName) {
              const toInputName = inputName.replace(`[${currentLangCode}]`, `[${localeCode}]`);
              const toInput = toTab.find(`*[name="${toInputName}"]`);
              if (toInput.length) {
                if (toInput.hasClass('ckeditor5')) {
                  const id = toInput.attr('id');
                  const editor = window.CKEditors[id];
                  if (editor) {

                    editor.setData($(this).val());
                  }
                } else {
                  toInput.val($(this).val());
                }
              }
            }
          });
        }
      });

      showSuccessMessage('Copy ML info success');
    });
  }
}

function responseMessage() {
  const responseMessageStorage = localStorage.getItem('_message');
  if (responseMessageStorage) {
    // eslint-disable-next-line no-undef
    showSuccessMessage(responseMessageStorage);
  }
  localStorage.removeItem('_message');
}
